const WebFormList = ({ webFields, removeFromList }) => {
  const list = webFields.map(({ _id, fieldName, fieldInForm, fieldFormVisible }) =>
    fieldInForm ? (
      <li key={_id}>
        {fieldName} {fieldFormVisible ? "-> visible in form" : null} 
        <button id={fieldForm} onClick={removeFromList}>
          X
        </button>
      </li>
    ) : null
  );

  return <ul>{list}</ul>;
};

export default WebFormList;
